.statList {
    display: flex;
  }
  
  .item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 50px;
    padding: 10px;
    color: #fff;
  }
  
  .statistics {
    display: flex;
    margin-top: 30px;
    border-radius: 5px;
    box-shadow: -3px 5px 12px -3px rgba(0, 0, 0, 0.75);
  }
  
  .title {
    font-size: 24px;
    color: #333333;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 1.2px;
  }